"use client";

import { useState } from "react";
import {
  Box,
  Button,
  FormControl,
  FormErrorMessage,
  FormLabel,
  Heading,
  Icon,
  Input,
  Select,
  SimpleGrid,
  Text,
  Textarea,
  VStack,
  useColorModeValue,
  useToast,
} from "@chakra-ui/react";
import { StarIcon, InfoIcon, AtSignIcon } from "@chakra-ui/icons";
import AnimatedSection from "../../components/layout/AnimatedSection";

const involvementOptions = [
  {
    title: "Parents & Families",
    description:
      "Walk alongside your student in STEM. Help with transportation, meals, event support, and spreading the word so more students can access high-quality STEM opportunities.",
    icon: StarIcon,
  },
  {
    title: "Mentors & Volunteers",
    description:
      "Share your experience in engineering, technology, or education and mentor the next generation. Whether you're a FIRST alum, student, or professional, your time has a direct impact.",
    icon: InfoIcon,
  },
  {
    title: "Sponsors & Partners",
    description:
      "Invest in tools, materials, and experiences that unlock STEM for underrepresented students. As a 501(c)(3), we work with companies, foundations, and individuals to build long-term impact.",
    icon: AtSignIcon,
  },
];

export default function GetInvolvedPage() {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    role: "",
    message: "",
  });
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const toast = useToast();

  const headingColor = useColorModeValue("text.light", "text.dark");
  const subTextColor = useColorModeValue("text.mutedLight", "text.mutedDark");
  const cardBg = useColorModeValue("surface.light", "surface.dark");
  const cardBorder = useColorModeValue("blackAlpha.100", "whiteAlpha.200");
  const cardText = useColorModeValue("text.mutedLight", "text.mutedDark");
  const accentColor = useColorModeValue("accent.500", "accent.400");
  const inputBg = useColorModeValue("white", "brand.900");
  const inputBorder = useColorModeValue("blackAlpha.100", "whiteAlpha.200");

  const formControlProps = {
    bg: inputBg,
    borderWidth: "1px",
    borderColor: inputBorder,
    _focusWithin: {
      borderColor: "accent.500",
      shadow: "0 0 0 1px var(--chakra-colors-accent-500)",
    },
  };

  const handleChange = (field) => (event) => {
    setFormData((prev) => ({ ...prev, [field]: event.target.value }));
  };

  const validate = () => {
    const newErrors = {};
    if (!formData.firstName.trim())
      newErrors.firstName = "First name is required.";
    if (!formData.lastName.trim())
      newErrors.lastName = "Last name is required.";
    if (!formData.email.trim()) {
      newErrors.email = "Email is required.";
    } else if (!/^\S+@\S+\.\S+$/.test(formData.email)) {
      newErrors.email = "Enter a valid email.";
    }
    if (!formData.role) newErrors.role = "Please tell us who you are.";
    return newErrors;
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    const validationErrors = validate();
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
      return;
    }

    setErrors({});
    setIsSubmitting(true);

    try {
      const response = await fetch("/api/contact", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        const errorBody = await response.json().catch(() => ({}));
        throw new Error(
          errorBody.error || "We couldn't send your message. Please try again."
        );
      }

      toast({
        title: "Thanks for reaching out!",
        description: "We'll be in touch soon.",
        status: "success",
        duration: 6000,
        isClosable: true,
      });

      setFormData({
        firstName: "",
        lastName: "",
        email: "",
        role: "",
        message: "",
      });
    } catch (error) {
      toast({
        title: "Message not sent",
        description: error.message,
        status: "error",
        duration: 7000,
        isClosable: true,
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <>
      <AnimatedSection variant="none" py={{ base: 16, md: 24 }}>
        <VStack spacing={4} textAlign="center">
          <Heading
            as="h1"
            size={{ base: "2xl", md: "3xl" }}
            color={headingColor}
          >
            Get Involved
          </Heading>
          <Text
            fontSize={{ base: "lg", md: "xl" }}
            maxW="3xl"
            color={subTextColor}
          >
            Join us in making a difference. Whether you're a parent, mentor, or
            sponsor, there are many ways to contribute to our mission.
          </Text>
        </VStack>
      </AnimatedSection>

      <AnimatedSection variant="default">
        <VStack spacing={10}>
          <VStack spacing={3} textAlign="center">
            <Heading as="h2" size="xl" color={headingColor}>
              How You Can Help
            </Heading>
            <Text color={subTextColor} maxW="3xl">
              Choose the path that fits you best. Every role supports students
              discovering what's possible through STEM.
            </Text>
          </VStack>

          <SimpleGrid columns={{ base: 1, md: 3 }} spacing={6} w="100%">
            {involvementOptions.map((option) => (
              <VStack
                key={option.title}
                align="flex-start"
                spacing={4}
                p={6}
                bg={cardBg}
                borderRadius="lg"
                borderWidth="1px"
                borderColor={cardBorder}
                shadow="lg"
              >
                <Icon as={option.icon} boxSize={10} color={accentColor} />
                <Heading as="h3" size="lg" color={headingColor}>
                  {option.title}
                </Heading>
                <Text color={cardText}>{option.description}</Text>
              </VStack>
            ))}
          </SimpleGrid>
        </VStack>
      </AnimatedSection>

      <AnimatedSection variant="alt">
        <Box
          w="100%"
          maxW="3xl"
          mx="auto"
          bg={cardBg}
          borderRadius="xl"
          borderWidth="1px"
          borderColor={cardBorder}
          shadow="xl"
          p={{ base: 6, md: 8 }}
        >
          <VStack as="form" spacing={6} align="stretch" onSubmit={handleSubmit}>
            <VStack spacing={2} textAlign="center">
              <Heading as="h2" size="lg" color={headingColor}>
                Contact Us
              </Heading>
              <Text color={subTextColor}>
                Tell us how you'd like to get involved. We'll reply from
                info@stemcycles.com.
              </Text>
            </VStack>

            <SimpleGrid columns={{ base: 1, md: 2 }} spacing={4}>
              <FormControl isRequired isInvalid={Boolean(errors.firstName)}>
                <FormLabel>First Name</FormLabel>
                <Input
                  placeholder="Your first name"
                  value={formData.firstName}
                  onChange={handleChange("firstName")}
                  {...formControlProps}
                />
                <FormErrorMessage>{errors.firstName}</FormErrorMessage>
              </FormControl>

              <FormControl isRequired isInvalid={Boolean(errors.lastName)}>
                <FormLabel>Last Name</FormLabel>
                <Input
                  placeholder="Your last name"
                  value={formData.lastName}
                  onChange={handleChange("lastName")}
                  {...formControlProps}
                />
                <FormErrorMessage>{errors.lastName}</FormErrorMessage>
              </FormControl>
            </SimpleGrid>

            <FormControl isRequired isInvalid={Boolean(errors.email)}>
              <FormLabel>Email</FormLabel>
              <Input
                type="email"
                placeholder="you@example.com"
                value={formData.email}
                onChange={handleChange("email")}
                {...formControlProps}
              />
              <FormErrorMessage>{errors.email}</FormErrorMessage>
            </FormControl>

            <FormControl isRequired isInvalid={Boolean(errors.role)}>
              <FormLabel>I am a...</FormLabel>
              <Select
                placeholder="Select an option"
                value={formData.role}
                onChange={handleChange("role")}
                {...formControlProps}
              >
                <option value="parent">Parent</option>
                <option value="mentor">Potential Mentor</option>
                <option value="sponsor">Potential Sponsor</option>
                <option value="other">Other</option>
              </Select>
              <FormErrorMessage>{errors.role}</FormErrorMessage>
            </FormControl>

            <FormControl>
              <FormLabel>Message</FormLabel>
              <Textarea
                placeholder="Tell us how you'd like to get involved..."
                rows={4}
                value={formData.message}
                onChange={handleChange("message")}
                resize="vertical"
                {...formControlProps}
              />
            </FormControl>

            <Button
              type="submit"
              colorScheme="brand"
              size="lg"
              w="100%"
              isLoading={isSubmitting}
              loadingText="Sending..."
            >
              Submit
            </Button>
          </VStack>
        </Box>
      </AnimatedSection>
    </>
  );
}
